/*ANIMATIONS*/
@-webkit-keyframes pump {
    0% {width: 50px; height: 50px;}
    83% {width: 128px; height: 128px;}
    85% {width: 130px; height: 130px;}
    100% {width: 50px; height: 50px;}
}
@keyframes pump {
    0% {width: 50px; height: 50px;}
    83% {width: 128px; height: 128px;}
    85% {width: 130px; height: 130px;}
    100% {width: 50px; height: 50px;}
}
@-webkit-keyframes ray {
    0% {width: 30px; height: 30px; opacity: 1; border-width: 1px;}
    100% {width: 200px; height: 200px; opacity: 0; border-width: 5px;}
}
@keyframes ray {
    0% {width: 30px; height: 30px; opacity: 1; border-width: 1px;}
    100% {width: 200px; height: 200px; opacity: 0; border-width: 5px;}
}
@-webkit-keyframes particle {
    0% {left:0%; top:0%; opacity: 0;}
    70% {opacity: 1;}
    100% {left:50%; top:50%; opacity: 1;}
}
@keyframes particle {
    0% {left:0%; top:0%; opacity: 0;}
    70% {opacity: 1;}
    100% {left:50%; top:50%; opacity: 1;}
}



