body{
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
/* Basic CSS Reset */
html, body, div, article, section, header,
h1, h2, h3, h4, h5, h6,
ul, li, ol, p, img {
  margin:0;
  padding:0;
}

/* Roadmap */

.list {
  text-align: start;
  position: flex;
}
.list h2 {
  color: #fff;
  font-weight: 700;
  letter-spacing: 1px;
  margin-bottom: 10px;
}
.list ul {
  position: relative;
}
.list ul li {
  position: relative;
  left: 0;
  color: #fce4ec;
  list-style: none;
  margin: 4px 0;
  border-left: 0.5px solid tomato;
  transition: 0.5s;
  cursor: pointer;
}
.list ul li:hover {
  left: 10px;
}
.list ul li span {
  /* position: relative; */
  padding: 8px;
  font-size: 16px;
  padding-left: 12px;
  display: inline-block;
  z-index: 1;
  transition: 0.5s;
}
.list ul li:hover span {
  position: relative;
  color: #111;
}
.list ul li:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #979887;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: 0.5s;
}
.list ul li:hover:before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}


/* Text */

.newsText{
  position: relative;
  font-size: 16px;
  font-family: 'Staatliches', sans-serif;
  text-transform: uppercase;
  font-display: swap;
  /* text-shadow: 0 0 10px rgb(232, 227, 226); */
  color: tomato;
  text-align: start;
}



.headerNews{
  /* text-align: flex-start;
  align-items: flex-start; */
  font-size: 42px;
  color: white;
  padding: 5rem;
  /* font-size: 32; */
  font-weight: 500;
}

.roadmapSection{
  color: white;
  padding-top: 120%;
  padding-left: 30%;
}
.sortByTitle{
  text-align: start;
  padding-top: 4rem;
  font-size: 18px;
  color: rgb(113, 112, 112);
}

.leftList{
  padding-top: 20% !important;
  /* text-align: flex-start;
  align-items: flex-start; */
  color: white;
  
  /* padding: 2rem; */

}

.leftListTitle{
  color: white;
  padding-left: 10%;
  text-align: start;
  /* font-size: 24px; */
}


.haederContainer{
  padding-top: 20%;
  }

img {
  border:none;
}
/* Global Styles */ 

a {
  text-decoration: none;
  font-weight: bold;
  font-weight: 400;
}

.widthWrapper {
  /* max-width: 750px; */
  margin: 0 auto;
}

/* Post Index */
#postIndex {
  margin: 3em auto;
}

article {
  font-family: 'Staatliches', sans-serif;
  margin-top: -0.063em;
}

article a {
  font-family: 'Staatliches', sans-serif;
  display: block;
  padding: 2.374em 2.5em 2.5em 2.5em;
  overflow: hidden;
  border-top: .063em solid #ccc;
  border-bottom: .063em solid #ccc;
}

article a:hover {
  background: #eee;
  border-top: #666 solid .25em;
  padding: 2.187em 2.5em 2.5em 2.5em;
}

.postImg {
  float: left;
  width: 25%;
  box-sizing: border-box;
  padding-right: 2em;
}

.postImg img {
  width: 100%;
}

article a h2 {
  font-family: 'Staatliches', sans-serif;
  color: rgb(213, 211, 203);
  float: right; 
  width: 75%;
  text-align: start;
}

article a:hover h2 {
  color: #157EBF;
}

article a p {
  color: rgb(101, 101, 101);
  float: right; 
  width: 75%;
  text-align: start;
  font-size: 18px;
}

article a:hover p {
  color: rgb(61, 61, 61);
}

@media only screen and (max-width: 750px) {
  #postIndex {
    width: 100%;
  }

  article a {
    padding: 2.374em 1.5em 2.5em 1.5em;
  }

  article a:hover {
    padding: 2.187em 1.5em 2.5em 1.5em;
  }
}

@media only screen and (max-width: 481px) {
  .postImg {
    display: none;
  }

  article a p, article a h2 {
    width: 100%;
  }

}


.firefly {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 0.4vw;
  height: 0.4vw;
  margin: -0.2vw 0 0 9.8vw;
  -webkit-animation: ease 200s alternate infinite;
          animation: ease 200s alternate infinite;
  pointer-events: none; }
  .firefly::before, .firefly::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -webkit-transform-origin: -10vw;
            transform-origin: -10vw; }
  .firefly::before {
    background: black;
    opacity: 0.4;
    -webkit-animation: drift ease alternate infinite;
            animation: drift ease alternate infinite; }
  .firefly::after {
    background: white;
    opacity: 0;
    box-shadow: 0 0 0vw 0vw yellow;
    -webkit-animation: drift ease alternate infinite, flash ease infinite;
            animation: drift ease alternate infinite, flash ease infinite; }

.firefly:nth-child(1) {
  -webkit-animation-name: move1;
          animation-name: move1; }
  .firefly:nth-child(1)::before {
    -webkit-animation-duration: 10s;
            animation-duration: 10s; }
