.speaker-form-header {
    text-align: start;
    background-color: transparent;
    /* border: 0.5px solid #D6D9DC; */
    border-radius: 3px;
    color: white;
    width: 100%;
    margin: 40px 0;
    
  }
  
  .speaker-form-header h1 {
    font-size: 30px;
    margin-bottom: 20px;
    color: white;
    /* width: 20vw; */
  }
  
  .speaker-form {
    background-color: transparent;
    border: 0.5px solid #D6D9DC;
    border-radius: 3px;
    color: white;
    width: 100%;
    padding: 50px;
    margin: 0 0 40px 0;
  }
  
  .form-row {
    margin-bottom: 40px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: wrap;
  }
  
  .form-row input[type="text"],
  .form-row input[type="email"] {
    background-color: #fff;
    border: 1px solid #D6D9DC;
    border-radius: 3px;
    width: 100%;
    padding: 7px;
    font-size: 14px;
  }
  
  .form-row label {
    margin-bottom: 15px;
  }
  
  .form-row label {
    text-align: right;
    width: 120px;
    margin-top: 7px;
    padding-right: 20px;
   }
    
  .legacy-form-row {
      margin-bottom: 10px;
    }
  
  .legacy-form-row legend {
      width: 120px;
      text-align: right;
      padding-right: 20px;
    }
    
    .legacy-form-row legend {
      float: left;
    }
    .form-row .instructions {
      margin-left: 120px;
    }
  
  .form-row input[type="text"]:invalid,
  .form-row input[type="email"]:invaid {
    border: 1px solid #D55C5F;
    color: #D55C5F;
    box-shadow: none;
  }
  
  .legacy-form-row {
    border: none;
    margin-bottom: 40px;
  }
  
  .legacy-form-row legend {
    margin-bottom: 15px;
  }
  
  .legacy-form-row .radio-label {
    display: block;
    font-size: 14px;
    padding: 0 20px 0 10px;
  }
  
  .legacy-form-row input[type="radio"] {
    margin-top: 2px;
  }
  
  .legacy-form-row .radio-label,
  .legacy-form-row input[type="radio"] {
    float: left;
  }
  
  .form-row select {
    width: 100%;
    padding: 5px;
    font-size: 14px;
  }
  
  .form-row textarea {
    font-family: "Helvetica", "Arial", sans-serif;
    font-size: 14px;
    
    border: 1px solid #D6D9DC;
    border-radius: 3px;
    
    min-height: 200px;
    margin-bottom: 10px;
    padding: 7px;
    resize: none;
  }
  
  .form-row .instructions {
    color: #999999;
    font-size: 14px;
    margin-bottom: 30px;
  }
  
  .form-row .checkbox-label {
    margin-bottom: 0;
  }
  
  .form-row button {
    font-size: 16px;
    font-weight: bold;
    
    color: #fff;
    background-color: #5995DA;
    
    border: none;
    border-radius: 3px;
    
    padding: 10px 40px;
    cursor: pointer;
  } 
  
  .form-row button:hover {
    background-color: #76AEED;
  }
  
  .form-row button:active {
    background-color: #407FC7;
  }
  
  
  @media only screen and (min-width: 700px) {
    .speaker-form-header,
    .speaker-form {
      width: 600px;
    }
    .form-row {
      flex-direction: row;
      align-items: flex-start;
      margin-bottom: 20px;
    }
    .form-row input[type="text"],
    .form-row input[type="email"],
    .form-row select,
    .form-row textarea {
      width: 250px;
      height: auto;
      height: initial;
    }
    
    .form-row .checkbox-label {
      margin-left: 120px;
      width: auto;
    }
    
    .form-row button {
        border: 0.5px solid white;
        background-color: transparent;
      margin-left: 120px;
    }
    .form-row button:hover {
        background-color: purple;
      margin-left: 120px;
    }

    
  }
