/* Card designing */

ul {
  margin: 0;
  padding: 0;
  width: 100%;
}
li {
  list-style: none;
  margin: 0 0 0 0;
  display: block;
  padding: 0 0 0 0;
}
li p {
  padding: 0.5em 0 0 1.5em;
  margin: 0;
  position: relative;
  display: inline-block;
  color: transparent;
  letter-spacing: 3px;
  font: 300 1.5em "Josefin Sans", sans-serif;
  cursor: pointer;
  transition: all 400ms;
  text-shadow: 0 0 0 rgba(255, 255, 255, 0.9), 0 1px 0 #0d0d0d;
}
li p:hover {
  font-weight: 100;
  letter-spacing: 4px;
  text-shadow: 0 0 0 white, 0 0 4px black, 0 -10px 15px white, 0 3px 25px #d9d9d9;
}
li p:hover:before {
  font-weight: 400;
  color: white;
}
li p:before {
  content: "~";
  position: absolute;
  left: 0;
  padding: 0 0 0 0;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  display: block;
  font-weight: 400;
  text-shadow: 0 0 0 rgba(255, 255, 255, 0.3), 1px 0 0 #0d0d0d;
}
.dark_body {
  background: #212121;
}
.ghost {
  text-shadow: 0 0 10px white !important;
}
.ghost:before {
  text-shadow: 0 0 10px white !important;
